<template>
    <el-container>
        <el-main>    
            <bread-crumb :items="breadCrumb"></bread-crumb>
            <div class="hd"> </div>
            <div></div>
            <div class="gap-2">
                <el-button style="width: 140px" type="danger" @click="handleReject"></el-button>
                <el-button style="width: 140px" type="primary" @click="handleApprove"></el-button>
                <el-button style="width: 140px" type="" @click="handleClose"></el-button>
            </div>
            <h2 class="gap-2"></h2>
            <el-table
                :data="historyRecords"
                stripe
                style="width: 100%; margin-bottom: 40px;">
                <el-table-column
                prop="commitDate"
                label=""
                width="180">
                </el-table-column>
                <el-table-column
                prop="verifyDate"
                label=""
                width="180">
                </el-table-column>
                <el-table-column
                prop="verifyStatus"
                label="">
                </el-table-column>
                <el-table-column
                prop="rejectReason"
                label="">
                </el-table-column>
                <el-table-column
                prop="verifyPerson"
                label="">
                </el-table-column>
            </el-table> 
            <el-dialog :title="title" :visible.sync="dialogVisible" width="30%">
                <textarea name="" id="" style="width:100%;" rows="10" placeholder="" v-model="textarea"></textarea>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="dialogVisible = false"> </el-button>
                    <el-button type="primary" @click="dialogVisible = false"> </el-button>
                </span>
            </el-dialog>
        </el-main>
    </el-container> 
</template>

<script>
import BreadCrumb from '@/components/common/BreadCrumb';

export default {
    name:'ProfessionEndVerify',
    components:{BreadCrumb},
    data(){
        return {
            breadCrumb: [{text:''},{text: ""},{text:''}],
            historyRecords:[],// 
            searInfoList:[],
            dialogVisible:false,
            textarea:'',
            title:''
        }
    },
    methods:{
        handleReject(){
            this.dialogVisible=true;
            this.title='';
        },
        handleApprove(){
            this.dialogVisible=true;
            this.title='';
        },
        handleClose(){
            history.back();
        }
    }
}
</script>

<style>
    .hd{
        margin: 30px 0;
    }
    .skip{
        color:blue;
    }
</style>





