<template>
    <el-dialog title="" :visible.sync="dialogVisible" width="30%" :before-close="handleClose">
        <el-form :model="form" :rules="rules"  ref="form" label-width="110px" class="demo-form">
            <el-row>
                <el-col :span="12">
                    <el-form-item label="" prop="bankType" class="tl">
                        <el-input v-model="form.bankType"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" prop="subBranch">
                        <el-input v-model="form.subBranch"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="" prop="name" class="tl">
                        <el-input v-model="form.name"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" prop="account">
                        <el-input v-model="form.account"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
            <span slot="footer" class="dialog-footer">
            <el-button @click="submitForm" type="primary"></el-button>
            <el-button @click="resetForm"></el-button>
        </span> 
    </el-dialog>
</template>

<script>
export default {
    name:'commission',
    props:['currentCompanyInfo'],
    data(){
        return {
            dialogVisible:false,
            form:{
                account:'',//
                bankType:'',// 
                name: '',//
                subBranch: '',// 
            },
            rules: {
                bankType:[{ required: true, message: '', trigger: 'blur' }],
                subBranch: [{ required: true, message: '', trigger: 'blur' }],
                name: [{ required: true, message: '', trigger: 'blur' },],
                account:[{ required: true, message: '', trigger: 'blur' }]
            }
        }
    },
    methods:{
        submitForm() {
            this.$refs.form.validate((valid) => {
                if (valid) {
                    this.dialogVisible=false;
                    alert('');
                    // ,Id;
                    this.form.companyId=this.currentCompanyInfo.companyId||'';
                    this.$http.post(this.$apiUrl.company.commission,this.form)
                        .then(function(data){
                            console.log(data,'');
                        })
                        .catch(function(err){
                            console.log(err,'');
                        });
                    // 
                    // this.$refs.form.resetFields();
                } else {
                    alert('')
                    return false;
                }
            });
        },
        resetForm() {
            this.$refs.form.resetFields();
            this.dialogVisible=false;
        },
        handleClose(done) {
            // x;
            this.$confirm('')
            .then(_ => { 
                // 
                this.$refs.form.resetFields();
                done();
            })
            .catch(_ => {
                //
            });
        },
        // ;
        open(){
            this.dialogVisible=true;
        }
    }
}
</script>

<style scoped>
    .el-dialog__footer{
        text-align: left;
    }
</style>





