<template>
    <el-dialog :title="title" :visible.sync="dialogVisible" width="50%" :before-close="handleClose">
        <el-form :model="form" :rules="rules" ref="form" label-width="120px">
            <el-form-item label="" prop="name" >
                <el-input v-model="form.name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="storeType">
                <el-select v-model="form.storeType" filterable placeholder="">
                        <el-option label="" value="1"></el-option>
                        <el-option label="" value="2"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="companyName" >
                <el-input v-model="form.companyName"></el-input>
            </el-form-item>
            <el-form-item label="" prop="address">
                <el-input v-model="form.address"></el-input>
            </el-form-item>
            <el-row>
                <el-col :span="8">
                    <el-form-item label="" prop="city" label-width="60px">
                        <el-input v-model="form.city"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="" prop="districtId">
                        <el-input v-model="form.districtId"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="" prop="townId">
                        <el-input v-model="form.city"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-form-item label="" prop="lonlat" placeholder=",">
                <el-input v-model="form.lonLat"></el-input>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="submitForm('form')" type="primary"></el-button>
            <el-button @click="resetForm('form')"></el-button>
        </span>
    </el-dialog>
</template>

<script>
import Region from '@/components/common/Region';

export default {
    name:'editorStore',
    props:['currentStoreInfo','title'],
    components:{Region},
    data(){
        return {
            startLevel:2,//
            endLevel:4,//
            dialogVisible:false,
            form: {
                address:'',//
                agencyId:'',//Id
                city:'',//
                companyId:'',//Id
                companyName:'',//
                districtId:'',//Id
                name:'',//
                status:'',
                storeType:'',//1.2.
                storeState:'',//1.2
                townId:'',//Id
                lonLat:''//
            },
            cityList:'',//
            array:[],
            // 
            rules: {
                companyName: [{ required: true, message: '', trigger: 'blur' }],
                abbreviation: [{ required: true, message: '', trigger: 'blur' }],
                city: [{ required: true, message: '', trigger: 'blur' }],
                cash: [{ required: true, message: '', trigger: 'blur' }],
                code: [{ required: true, message: '', trigger: 'blur' }],
                bd: [{ required: true, message: 'bd', trigger: 'blur' }],
                districtId: [{ required: true, message: '', trigger: 'blur' }],
                townId:[{ required: true, message: '', trigger: 'blur' }],
                address:[{ required: true, message: '', trigger: 'blur' }],
                name:[{ required: true, message: '', trigger: 'blur' }],
                storeType:[{ required: true, message: '', trigger: 'blur' }]
            }
        }
    },
    methods:{
        open() {
            if(this.title==''){
                this.ruleForm=Object.assign({},this.currentStoreInfo);
            }else{
                this.ruleForm= {
                    address:'',//
                    area:'',//
                    companyName:'',//
                    plate:'',//
                    storeAdress:'',//
                    storeName:'',//
                    storeType:'',//
                }
            }
            this.dialogVisible = true;
        },
        handleClose(done) {
            this.$confirm('')
            .then(_ => { 
                this.$refs.ruleForm.resetFields(); 
                done();
            })
            .catch(_ => {});
        },
        submitForm(formName) {
            console.log(this.ruleForm,111)
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.dialogVisible=false;
                    alert('');
                    if(this.title==''){
                        this.$emit('editSuccess',this.ruleForm);
                    }else{
                        this.$emit('addSuccess',this.ruleForm);
                    };
                    // ;
                    // this.$refs[formName].resetFields();
                } else {
                    alert('')
                    return false;
                }
            });
        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
            this.dialogVisible=false;
        },
        //   ;
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handlePreview(file) {
            console.log(file);
        },
        handleExceed(files, fileList) {
            this.$message.warning(` 3  ${files.length}  ${files.length + fileList.length} `);
        },
        beforeRemove(file, fileList) {
            return this.$confirm(` ${ file.name }`);
        } 
    }
}
</script>

<style>

</style>




