<template>
    <div>
        <div class="search-result" > 956</div>
        <el-table :data="searInfoList" border style="width: 100%">
            <el-table-column prop="name" label="/" align="center" ></el-table-column>
            <el-table-column prop="submitPeople" label="" align="center" ></el-table-column>
            <el-table-column prop="auditPeople" label="" align="center" ></el-table-column>
            <el-table-column  label="" align="center" >
                <template slot-scope="scope">
                    <div><span class="circle"></span><span></span></div>
                </template>
            </el-table-column>
            <el-table-column prop="endReason" label="" align="center"></el-table-column>
            <el-table-column prop="endReason" label="" align="center">
                <template slot-scope="scope">
                    <el-button size="mini" @click="audit(scope.$index, scope.row)" type="text"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="block">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="pagination.currentPage"
                :page-sizes="[10, 2, 3, 400]"
                :page-size="pagination.pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pagination.total">
            </el-pagination>
        </div>
    </div>  
</template>

<script>
export default {
    name:'waitAudit',
    data(){
        return {
            // 
            pagination:{
                currentPage:1,//1;
                pageSize:10,//10
                total:400//
            },
            tableData:[
                {
                    name:'key',
                    submitPeople:'wk',
                    auditPeople:'',
                    auditTime:'2018-12-05',
                    endReason:''
                },
                {
                    name:'key',
                    submitPeople:'wk',
                    auditPeople:'',
                    auditTime:'2018-12-05',
                    endReason:''
                },
                {
                    name:'key',
                    submitPeople:'wk',
                    auditPeople:'',
                    auditTime:'2018-12-05',
                    endReason:''
                },
                {
                    name:'key',
                    submitPeople:'wk',
                    auditPeople:'',
                    auditTime:'2018-12-05',
                    endReason:''
                }
            ]
        }
    },
    methods:{
        //
        handleSizeChange(val) {
            this.pagination.pageSize=val;
        },
        //
        handleCurrentChange(val) {
            this.pagination.currentPage=val;
        },
        audit(){}
    },
    computed:{
        //
        searInfoList(){
            return this.tableData.slice((this.pagination.currentPage-1)*this.pagination.pageSize,this.pagination.currentPage*this.pagination.pageSize); 
        }
    },
}
</script>

<style>

</style>





