<template>
    <el-dialog :title="title" :visible.sync="dialogVisible" width="50%" :before-close="handleClose">
        <el-form :model="form" :rules="rules" ref="form" label-width="110px">
            <el-row>
                <el-col :span="12">
                    <el-form-item label="" prop="name" class="tl">
                        <el-input v-model="form.name" placeholder="50"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" prop="abbreviation">
                        <el-input v-model="form.abbreviation" placeholder="50"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="" prop="cityList" class="tl">
                        <region v-model="form.cityList" :startLevel="startLevel" :endLevel="endLevel"></region>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" prop="deposit">
                        <el-input v-model="form.deposit"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="" prop="organizationCode" class="tl">
                        <el-input v-model="form.organizationCode" placeholder="50"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item prop="businessType" label="" placeholder="">
                        <el-select v-model="form.businessType" filterable>
                                <el-option label="" value="3"></el-option>
                                <el-option label="" value="1"></el-option>
                                <el-option label="" value="2"></el-option>
                            </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-form-item label="bd" prop="operator"  label-width="40px" class="tl">
                <el-input v-model="form.operator" placeholder=""></el-input>
            </el-form-item>
            <el-row>
                <el-col :span="12">
                    <el-row>
                        <el-col :span="10">
                            <el-form-item label="" prop="corporateStart" class="tl team-time">
                                <el-date-picker
                                    format="yyyy-MM-dd"
                                    v-model="form.corporateStart"
                                    type="date"
                                    placeholder=""
                                    style="width:150px"
                                    value-format="yyyy-MM-dd">
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                        <el-col :span="10">
                            <el-form-item prop="corporateEnd" label="" class="team-time">
                                <el-date-picker
                                    format="yyyy-MM-dd"
                                    v-model="form.corporateEnd"
                                    type="date"
                                    placeholder=""
                                    style="width:150px"
                                    value-format="yyyy-MM-dd">
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" prop="corporate">
                        <el-input v-model="form.corporate"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="" class="tl" prop="corporatePhone">
                        <el-input v-model="form.corporatePhone" ></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" prop="address" >
                        <el-input v-model="form.address"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-form-item label="" class="tl" prop="agencyId">
                <el-select v-model="form.agencyId" placeholder="">
                    <el-option label="" value="0"></el-option>
                    <el-option
                        v-for="item in agencyList"
                        :key="item.agencyId"
                        :label="item.agencyName"
                        :value="item.agencyId">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item  class="tl upload">
                <el-upload
                class="upload-demo"
                action="https://jsonplaceholder.typicode.com/posts/"
                :on-preview="handlePreview"
                :on-remove="handleRemove"
                :before-remove="beforeRemove"
                multiple
                :limit="3"
                :on-exceed="handleExceed">
                    <el-button size="small" type="primary"></el-button>
                    <div slot="tip" class="el-upload__tip">(txt,doc,docx,xls,xlsx,ppt,pptx,pdf,jpg,jpeg,png,gif,rar,zip)</div>
                </el-upload>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="submitForm('form')" type="primary"></el-button>
            <el-button @click="resetForm('form')"></el-button>
        </span>
    </el-dialog>
</template>

<script>
import Region from '@/components/common/Region'
export default {
    name:'editorCompany',
    props:['currentCompanyInfo','title'],
    components:{Region},
    data(){
        return {
            agencyList:[
                {
                    agencyId:'1',
                    agencyName:''
                },
                {
                    agencyId:"2",
                    agencyName:''
                }
            ],
            dialogVisible:false,
            startLevel:1,//
            endLevel:2,//
            form:{
                abbreviation:'',//
                address:'',//
                agencyId:'',//Id
                businessType:'',//,01.2.3.
                cityId:'',//Id
                cityList:[],
                corporate:'',//
                corporatePhone:'',//
                corporateStart:'',//
                corporateEnd:'',//
                deposit:'',//
                name:'',//
                organizationCode:'',//
                operator:'',//
                provinceId:'',//Id
                resourceKey:'',//key
                state:''//1.2. 
            },
            // 
            rules: {
                name: [{ required: true, message: '', trigger: 'blur' }],
                abbreviation: [{ required: true, message: '', trigger: 'blur' }],
                cityList: [{ required: true, message: '', trigger: 'blur' }],
                deposit: [{ required: true, message: '', trigger: 'blur' }],
                organizationCode: [{ required: true, message: '', trigger: 'blur' }],
                corporateStart: [{ required: true, message: '', trigger: 'blur' }],
                corporateEnd: [{ required: true, message: '', trigger: 'blur' }]
            }
        }
    },
    created(){
        // ;
        this.$http.get(this.$apiUrl.common.agency)
            .then(function(data){
                 console.log(data,'');
            })
            .catch(function(err){
                console.log(err,'');
            })
    },
    methods:{
        open() {
            if(this.title==''){
                this.form=Object.assign({},this.currentCompanyInfo,{cityList:[this.currentCompanyInfo.provinceId,this.currentCompanyInfo.cityId]});
            }else{
                this.form={
                    abbreviation:'',//
                    address:'',//
                    agencyId:'',//Id
                    businessType:'',//,1.2.3.
                    cityId:'',//
                    cityList:[],//
                    corporate:'',//
                    corporatePhone:'',//
                    corporateStart:'',//
                    corporateEnd:'',//
                    deposit:'',//
                    name:'',//
                    organizationCode:'',//
                    operator:'',//
                    provinceId:'',//Id
                    resourceKey:'',//key 
                }
            }
            this.dialogVisible = true;
        },
        handleClose(done) {
            this.$confirm('')
            .then(_ => { 
                this.$refs.form.resetFields(); 
                done();
            })
            .catch(_ => {});
        },
        submitForm(formName) {
            this.$refs.form.validate((valid) => {
                if (valid) {
                    this.dialogVisible=false;
                    alert('');
                    if(this.title==''){
                        this.$emit('editSuccess',this.form);
                    }else{
                        if(this.form.cityList.length){
                            this.form.provinceId=this.form.cityList[0];
                            this.form.cityId=this.form.cityList[1];
                        }
                        let realForm=Object.assign({},this.form);
                        delete realForm.cityList;
                        this.$http.put(this.$apiUrl.company.add,realForm)
                            .then(function(data){
                                console.log(data);
                            })
                            .catch(function(error){
                                console.log(error)
                            });
                        this.$emit('addSuccess',realForm);
                    };
                    // ;
                    // this.$refs[formName].resetFields();
                } else {
                    alert('')
                    return false;
                }
            });
        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
            this.dialogVisible=false;
        },
        //   ;
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handlePreview(file) {
            console.log(file);
        },
        handleExceed(files, fileList) {
            this.$message.warning(` 3  ${files.length}  ${files.length + fileList.length} `);
        },
        beforeRemove(file, fileList) {
            return this.$confirm(` ${ file.name }`);
        } 
    }
}
</script>

<style>
    .tl .el-form-item__label{
        text-align: left;
    }
    .upload .el-form-item__content{
        margin-left: 0!important;
    }
    .team-time .el-form-item__error{
        width:120px;
    }
</style>




