<template>
    <el-container>
        <el-main>    
            <bread-crumb :items="breadCrumb"></bread-crumb>
            <div style="text-align:right;">
                <el-button type="primary" @click="addAgent"></el-button>
            </div>     
            <el-form ref="form" :model="form" label-width="180px" class="gap-2">
                <el-row >
                    <el-col :span="12">
                        <el-form-item label="" prop="city">
                            <el-select v-model="form.city" placeholder="" filterable>
                                <el-option
                                    v-for="(item,index) in city"
                                    :key="index"
                                    :label="item"
                                    :value="item">
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="" prop="storeAddress">
                            <el-date-picker
                                        format="yyyy-MM-dd"
                                        v-model="form.timeStart"
                                        type="date"
                                        placeholder=""
                                        value-format="yyyy-MM-dd">
                                    </el-date-picker>
                        </el-form-item>
                        <el-form-item label="" prop="business">
                            <el-select v-model="form.business" filterable>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="" prop="storeAddress">
                            <el-input v-model="form.storeAddress" style="width:220px;"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="" prop="companyName">
                            <el-select v-model="form.business" filterable>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="" prop="storeAddress">
                            <el-date-picker
                                        format="yyyy-MM-dd"
                                        v-model="form.timeStart"
                                        type="date"
                                        placeholder=""
                                        value-format="yyyy-MM-dd">
                            </el-date-picker>
                        </el-form-item>
                        <el-form-item label="" prop="storeName">
                            <el-input v-model="form.storeName" style="width:220px;"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <el-row>
                <el-col :span="2" :offset="10">
                    <el-button class="reset" @click="resetForm('form')"></el-button>
                </el-col>
                <el-col :span="2">
                    <el-button type="primary" @click="search('form')"></el-button>
                </el-col>
            </el-row>
            <div class="search-result"> 95656900</div>
            <el-table :data="searInfoList" border style="width: 100%">
                <el-table-column prop="date" label="" align="center" ></el-table-column>
                <el-table-column prop="name" label="" align="center" ></el-table-column>
                <el-table-column prop="address" label="" align="center" ></el-table-column>
                <el-table-column prop="address" label="" align="center" ></el-table-column>
                <el-table-column prop="address" label="" align="center"></el-table-column>
                <el-table-column prop="name" label="" align="center"></el-table-column>
                <el-table-column prop="name" label="" align="center"></el-table-column>
                <el-table-column prop="name" label="" align="center"></el-table-column>
                <el-table-column prop="name" label="" width="300px" align="center">
                    <template slot-scope="scope">
                        <el-button size="mini" @click="detail(scope.$index, scope.row)" type="text" v-if="false">|</el-button>
                        <el-tooltip placement="right" effect="light" v-if="true">
                            <el-button type="text" size="mini"></el-button>
                            <div slot="content" @click="edit(scope.$index)"></div>
                            <div slot="content" class="cz" @click="followUp" ></div>
                            <div slot="content" class="cz" @click="endJoin"></div>
                        </el-tooltip>
                        <el-button v-if="false" size="mini" @click="reSubmit(scope.$index, scope.row)" type="text"></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="block">
                <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="pagination.currentPage"
                    :page-sizes="[10, 2, 3, 400]"
                    :page-size="pagination.pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="pagination.total">
                </el-pagination>
            </div>
            <el-dialog title="!" :visible.sync="firstDialogVisible" width="30%" >
                <p>15,</p>
                <p>a</p>
                <p>b,</p>
                <span slot="footer" class="dialog-footer">
                    <el-button type="primary" @click="firstDialogVisible = false,secondDialogVisible = true"></el-button>
                </span>
            </el-dialog>
            <el-dialog title="!,," :visible.sync="secondDialogVisible" width="30%" >
                <textarea name="" id="" rows="10" placeholder="" v-model="noJoin" style="width:100%;"></textarea>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="secondDialogVisible = false,continueJoin()"> </el-button>
                    <el-button type="primary" @click="secondDialogVisible = false,NoJoin()" > </el-button>
                </span>
            </el-dialog>
        </el-main>
    </el-container> 
</template>

<script>
import BreadCrumb from '@/components/common/BreadCrumb';

export default {
  name: 'CompanyManagement',
  components:{BreadCrumb},
  data () {
    return {
        firstDialogVisible: false,//
        secondDialogVisible:false,//
        noJoin:'',//
        companyInfoIndex:'',//
        currentCompanyInfo:'',//
        title:'',//
        isShow:false,
        // 
        form: {
            agent:'',//
            business:'',//
            city:'',//
            companyName:'',//
            timeStart:'',//
            timeEnd:'',//
            storeName: '',//
            storeAddress:'',//
            timeOver:''//
        },
        // 
        pagination:{
            currentPage:1,//1;
            pageSize:10,//10
            total:400//
        },
        breadCrumb: [{text:''},{text: ""}],
        city:['','','',''],
        tableData: [{
          date: '2016-05-02',
          name: '',
          address: ' 1518 '
        }, {
          date: '2016-05-04',
          name: '',
          address: ' 1517 '
        }, {
          date: '2016-05-01',
          name: '',
          address: ' 1519 '
        }, {
          date: '2016-05-03',
          name: '',
          address: ' 1516 '
        }]
    }
  },
    methods:{
        // 
        edit(index){
            console.log(index,2222)
        },
        // 
        followUp(){

        },
        // ;
        endJoin(){
            this.firstDialogVisible=true;
        },
        // ;
        noJoin(){},
        // 
        detail(){},
        // 
        reSubmit(){},
        // 
        resetForm(formName) {
            this.$refs.form.resetFields();
        },
        //
        search(val){
            console.log(val,1111111)
            console.log(this.form);
        },
        //
        handleSizeChange(val) {
            this.pagination.pageSize=val;
        },
        //
        handleCurrentChange(val) {
            this.pagination.currentPage=val;
        },
        // 
        addAgent(){
             
        },
        //
        continueJoin(){
            this.noJoin='';
        }
    },
    computed:{
        //
        searInfoList(){
            return this.tableData.slice((this.pagination.currentPage-1)*this.pagination.pageSize,this.pagination.currentPage*this.pagination.pageSize);
        }
    }
}
</script>

<style>
    .search-result{
        color:gray;
        margin-bottom: 20px;
        margin-top: 50px;
    }
    .block{
        margin-top: 100px;
        text-align: center;
    }
    .el-button{
        margin-left: 0 !important;
    }
    .el-dialog__footer{
        text-align: right;
    }
    .cz{
        margin-top: 10px;
    }
</style>





