<template>
    <el-container>
        <el-main>
            <el-table :data="recordData" border style="width: 100%">
                <el-table-column prop="title" label="" align="center" >
                </el-table-column>
                <el-table-column prop="name" label="" align="center" ></el-table-column>
                <el-table-column prop="address" label="" align="center" ></el-table-column>
                <el-table-column prop="address" label="" align="center" ></el-table-column>
                <el-table-column prop="address" label="" align="center"></el-table-column>
            </el-table>
            <div class="gap-2">
                -->; xxx.jpg  
            </div>
        </el-main>
    </el-container>
</template>

<script>
export default {
    name:'Record',
    data(){
        return {
             tableData: [{
          date: '2016-05-02',
          name: '',
          address: ' 1518 '
        }, {
          date: '2016-05-04',
          name: '',
          address: ' 1517 '
        }, {
          date: '2016-05-01',
          name: '',
          address: ' 1519 '
        }, {
          date: '2016-05-03',
          name: '',
          address: ' 1516 '
        }]
        }
    },
    computed:{
        recordData(){
            let hd=['','','',''];
            this.tableData.forEach(function(v,i){
                v.title=hd[i];
            });
            return this.tableData;
        }
    }
}
</script>

<style>

</style>




